// Link
// ––––––––––––––––––––––––––––––––––––––––––––––––––

a {
	color: $color-primary;
	text-decoration: none;

	&:focus,
	&:hover {
		color: black;
		/*text-decoration: underline;*/
	}
}



.icon:hover svg path {
	fill: $blue;
  transition: 0.3s;
}

header a:hover {
	color: #0a0a48 !important;
}
