// Blockquote
// ––––––––––––––––––––––––––––––––––––––––––––––––––

blockquote {
	border-left: .3rem solid $color-primary;
	margin-left: 0;
	margin-right: 0;
	padding: 1rem 1.5rem;

	*:last-child {
		margin-bottom: 0;
	}
}

.instagram-media, .twitter-tweet {
	margin-bottom: 4rem !important;
	margin-top: 4rem !important;
}
